import React from 'react';
import { DelayLink } from './DelayLink';

export default function Dropdown({ list, className, handleHover, handleLeave, onHeaderLinkClick, dropdown, submenu, toggleSubmenu, submenuOpened, mobileScreen, dropdownOpened, onDelayStart, onDelayEnd }) {

    return (
        <div className={"dropdown " + (className || "") + (dropdownOpened ? " opened" : "")}>
            <div className="dropdown__wrapper" ref={dropdown}>
                <ul className="dropdown__list">
                    {list.map(item => {
                        return (
                            <li
                                className="dropdown__item"
                                key={item.id}
                                onMouseLeave={(item.submenu && !mobileScreen) ? toggleSubmenu : () => false}
                                onMouseEnter={(item.submenu && !mobileScreen) ? toggleSubmenu : () => false}
                            >
                                <DelayLink delay={500} to={item.to} className="dropdown__link" onMouseOver={handleHover} onMouseLeave={handleLeave} onClick={onHeaderLinkClick}>{item.title}</DelayLink>
                                {item.submenu && <button className={"open open--header open--dropdown" + (submenuOpened ? " active" : "")} onClick={toggleSubmenu}></button>}
                                {item.submenu && (
                                    <div className="dropdown__submenu" data-submenu ref={submenu}>
                                        <ul className="dropdown__submenu-list" data-submenu-content>
                                            {item.submenu.map(subitem => {
                                                return (
                                                    <li className="dropdown__item" key={subitem.id}>
                                                        <DelayLink delay={500} to={subitem.to} className="dropdown__link dropdown__link--submenu" onMouseOver={handleHover} onMouseLeave={handleLeave} onClick={onHeaderLinkClick}>
                                                            {subitem.title}
                                                        </DelayLink>
                                                    </li>
                                                );
                                            })}
                                        </ul>
                                    </div>
                                )}
                            </li>
                        );
                    })}
                </ul>
            </div>
        </div>
    )
}
