import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap/gsap-core';

export default function SlideIn({ children, setXPercent, setYPercent, toXPercent, toYPercent, opacity, trigger, toggleActions, delay, ...props }) {

    let item = useRef(null);

    useEffect(() => {

        gsap.set(item.current, {
            xPercent: setXPercent || null,
            yPercent: setYPercent || null,
            autoAlpha: opacity ? 0 : 1
        });

        let tl = gsap.timeline({
            scrollTrigger: {
                trigger: trigger || item.current,
                start: 'top center',
                toggleActions: toggleActions || 'play none none none'
            }
        }).to(item.current, {
            xPercent: toXPercent || 0,
            yPercent: toYPercent || null,
            autoAlpha: opacity || 1,
            delay: delay || 0
        });

        return () => {
            tl.kill();
        }
    }, [setXPercent, toXPercent, opacity, setYPercent, toYPercent, trigger, toggleActions, delay]);

    return (
        <div {...props} ref={item}>
            {children}
        </div>
    )
}
