import React, { useEffect, useRef, useContext } from 'react';
import { ScrollTrigger } from 'gsap/ScrollTrigger';
import { gsap } from 'gsap/gsap-core';
import ScrollToPlugin from 'gsap/src/ScrollToPlugin';
import { CursorContext } from './../../providers/CursorProvider';
import SlideIn from '../SlideIn';

gsap.registerPlugin(ScrollToPlugin);

export default function FixedNavSections({ sections, className, headerRef }) {
    let { handleHover, handleLeave } = useContext(CursorContext);
    let items = useRef([]);
    let navButtons = useRef([]);

    function onLinkClick(e) {
        e.preventDefault();
        const target = e.nativeEvent.target.getAttribute('href');
        let section = document.getElementById(target);
        gsap.to(window, {
            scrollTo:  section.getBoundingClientRect().top + window.scrollY - headerRef.current.clientHeight - (window.innerWidth < 480 ? 121 : 0),
            ease: 'Power4.inOut',
            duration: 0.5
        })
    }

    useEffect(() => {
        items.current.forEach((item, index) => {
            if (!item) return;

            ScrollTrigger.create({
                trigger: item,
                start: 'top center',
                end: () => `+=${item.clientHeight}`,
                toggleClass: {
                    className: 'active',
                    targets: navButtons.current[index]
                }
            });
        });
    }, [headerRef]);

    return (
        <div className={'sections ' + (className ? 'sections--' + className : '')}>
            <div className='sections__navigation'>
                <div className="sections__navigation-wrapper">
                    <h2 className='sections__navigation-title'>The Breakthrough</h2>
                    <ul className='sections__navigation-list' id='sections-swiper-pagination'>
                        {sections.map(item => {
                            return (
                                <li className='sections__navigation-item' key={item.id}>
                                    <a
                                        href={"section-" + item.id} className='sections__navigation-link'
                                        ref={el => navButtons.current.push(el)}
                                        onMouseOver={handleHover} onMouseLeave={handleLeave}
                                        onClick={onLinkClick}
                                    >{item.title}</a>
                                </li>
                            );
                        })}
                    </ul>
                </div>
            </div>
            <ul className='sections__list'>
                {sections.map(item => {
                    return (
                        <li className='sections__item' key={item.id} ref={el => items.current.push(el)} id={"section-" + item.id}>
                            <div className='sections__item-wrapper'>
                                <h1 className='sections__item-title'>
                                    <div className='sections__item-mask sections__item-mask--icon'>
                                        <SlideIn trigger={"#section-" + item.id} setXPercent={100} toXPercent={0}>
                                            <img src={item.icon} className='sections__item-icon' alt='' />
                                        </SlideIn>
                                    </div>
                                    <div className='sections__item-mask sections__item-mask--title'>
                                        <SlideIn trigger={"#section-" + item.id} setXPercent={-100} toXPercent={0}>{item.title}</SlideIn>
                                    </div>
                                </h1>
                                <div className='sections__item-mask sections__item-mask--text'>
                                    <SlideIn className='sections__item-text' trigger={"#section-" + item.id} setYPercent={100} toYPercent={0}>{item.text}</SlideIn>
                                </div>
                                <div className='sections__item-mask sections__item-mask--image'>
                                    <SlideIn className='sections__item-image-wrapper' trigger={"#section-" + item.id} setYPercent={100} toYPercent={0}>
                                        <img src={item.image} className='sections__item-image' alt='' />
                                    </SlideIn>
                                </div>
                            </div>
                        </li>
                    );
                })}
            </ul>
        </div>
    );
}