import React, { useContext, useEffect, useRef } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';

import parallaxAnimation from './../../../services/parallaxAnimation';

import blogBg from '../../../images/blog/bg.png';
import blogImage from '../../../images/blog/image.png';
import SideText from './../../../common/SideText';
import Arrow from './../../../common/icons/Arrow';
import Facebook from './../../../common/icons/Facebook';
import Twitter from './../../../common/icons/Twitter';
import LinkedIn from './../../../common/icons/LinkedIn';
import Pinterest from './../../../common/icons/Pinterest';
import Share from './../../../common/icons/Share';
import PageProgress from '../../../common/PageProgress';
import { LoadedContext } from './../../../providers/LoadedProvider';

let transition = { duration: 0.2, ease: [0.43, 0.13, 0.23, 0.96] };

export default function Blog({ linkProps, setLinkProps, handleHover, handleLeave, handleHide }) {
    let leftLinkRef = useRef(null);
    let rightLinkRef = useRef(null);
    let banner = useRef(null);
    let bannerImage = useRef(null);
    let loaded = useContext(LoadedContext);

    useEffect(() => {
        window.scrollTo(0, 0);
        document.body.classList.remove('no-scroll');

        parallaxAnimation(bannerImage.current, 20, window.innerWidth < 768 ? "top top" : "center center", banner.current);
    }, [])

    function handleLinkClick(e) {
        let link = e.currentTarget;

        link.classList.add('active');

        let linkCoords = link.getBoundingClientRect();

        setLinkProps({
            left: linkCoords.left,
            top: linkCoords.top - (document.getElementById('header').clientHeight),
            width: linkCoords.width,
            height: linkCoords.height,
        });

        document.body.classList.add('no-scroll');
        document.getElementById('banner-title').style.opacity = 0;
    }
    return (
        <motion.main initial='initial' animate='animate' exit='exit' className="main blog">
            {loaded ? <PageProgress /> : null}
            <div className="blog__banner" ref={banner}>
                <motion.div
                    initial={{
                        borderRadius: '5px',
                        ...linkProps,
                    }}
                    exit={{ opacity: 0 }}
                    animate={{
                        width: '100%',
                        height: '100%',
                        transition: { delay: 0.1, duration: 0.75, transition },
                        left: 0,
                        top: 0,
                        borderRadius: 0,
                    }}
                    className="blog__banner-image-wrapper"
                >
                    <div style={{ backgroundImage: `url(${blogBg})` }} className="blog__banner-image" ref={bannerImage} />
                </motion.div>
                <div className="blog__banner-wrapper container">
                    <motion.h1
                        initial={{ opacity: 0 }}
                        animate={{ opacity: 1 }}
                        transition={{ delay: 0.6, duration: 0.6 }}
                        className="blog__banner-title" id="banner-title">Clutch: Top Canadain Web Designers</motion.h1>
                </div>
            </div>
            <div className="blog__wrapper container container--blog" id="blog-wrapper">
                <SideText modificator="blog" wrapper="blog-wrapper">our thoughts</SideText>
                <motion.div transition={{ duration: 0.4, ease: [0.43, 0.13, 0.23, 0.96] }} animate={{ opacity: 1 }} className="blog__content">
                    <h1 className="blog__title">Clutch: Candian firms that deliver </h1>
                    <p className="blog__text">
                        Proin gravida nibh vel velit auctor aliquet. Aenean sollicitudin, lorem quis bibendum auctor, nisi elit consequat ipsum, nec sagittis sem nibh id elit. Duis sed odio sit amet nibh vulputate cursus a sit amet mauris. Morbi accumsan ipsum velit. Nam nec tellus a odio tincidunt auctor a ornare odio. Sed non  mauris vitae erat consequat auctor eu in elit. Class aptent taciti sociosqu ad litora torquent per conubia nostra, per inceptos himenaeos. Mauris in erat justo.
                    </p>
                    <img src={blogImage} className="blog__image" alt="" />
                    <p className="blog__text">
                        Nullam ac urna eu felis dapibus condimentum sit amet a augue. Sed non neque elit.
                        Sed ut imperdiet nisi. Proin condimentum fermentum nunc. Etiam pharetra, erat sed fermentum feugiat, velit mauris egestas quam,
                        ut aliquam massa nisl quis neque. Suspendisse in orci enim. Duis sed odio sit amet nibh vulputate cursus a sit amet mauris.
                        Morbi accumsan ipsum velit. Nam nec tellus a odio tincidunt auctor a ornare odio. Sed non  mauris vitae erat consequat auctor eu in elit.
                        <br />
                        <br />
                        Nullam ac urna eu felis dapibus condimentum sit amet a augue. Sed non neque elit. Sed ut imperdiet nisi.
                        Proin condimentum fermentum nunc. Etiam pharetra, erat sed fermentum feugiat, velit mauris egestas quam, ut aliquam massa nisl quis neque.
                        Suspendisse in orci enim. Duis sed odio sit amet nibh vulputate cursus a sit amet mauris. Morbi accumsan ipsum velit. Nam nec tellus a odio tincidunt
                        auctor a ornare odio. Sed non  mauris vitae erat consequat auctor eu in elit.
                    </p>
                </motion.div>
                <div className="blog__share">
                    <div className="blog__share-wrapper">
                        <button className="blog__share-button blog__share-button--big" onMouseOver={handleHide} onMouseLeave={handleLeave}>
                            <Share className="blog__share-icon" />
                        </button>
                        <button className="blog__share-button blog__share-button--small blog__share-button--1" onMouseOver={handleHide} onMouseLeave={handleLeave}>
                            <Facebook className="blog__share-icon" />
                        </button>
                        <button className="blog__share-button blog__share-button--small blog__share-button--2" onMouseOver={handleHide} onMouseLeave={handleLeave}>
                            <Twitter className="blog__share-icon" />
                        </button>
                        <button className="blog__share-button blog__share-button--small blog__share-button--3" onMouseOver={handleHide} onMouseLeave={handleLeave}>
                            <LinkedIn className="blog__share-icon" />
                        </button>
                        <button className="blog__share-button blog__share-button--small blog__share-button--4" onMouseOver={handleHide} onMouseLeave={handleLeave}>
                            <Pinterest className="blog__share-icon" />
                        </button>
                    </div>
                </div>
                <div className="blog__nav">
                    <Link to='/blogs/2'
                        className="blog__nav-button blog__nav-button--prev"
                        onClick={handleLinkClick}
                        ref={leftLinkRef}
                        onMouseOver={handleHover} onMouseLeave={handleLeave}
                    >
                        <motion.div
                            className="blog__nav-button-wrapper"
                            style={{ backgroundImage: `url(${blogBg})` }}
                            exit={() => {
                                let leftLink = leftLinkRef.current;
                                return leftLink.classList.contains('active') ? { opacity: 1 } : { opacity: 0 }
                            }}
                            animate={{ opacity: 1 }} transition={{ duration: 0.2, ...transition }}
                        >
                            <motion.h4 exit={{ opacity: 0 }} transition={{ duration: 0.2, ...transition }} className="blog__nav-title">
                                <Arrow className="blog__nav-icon" />
                                <span className="blog__nav-title-text">Previous Post</span>
                            </motion.h4>
                            <motion.p exit={{ opacity: 0 }} transition={{ duration: 0.2, ...transition }} className="blog__nav-text">Top Candian Web Designers</motion.p>
                        </motion.div>
                    </Link>
                    <Link to='/blogs/3'
                        className="blog__nav-button blog__nav-button--next"
                        onClick={handleLinkClick}
                        ref={rightLinkRef}
                        onMouseOver={handleHover} onMouseLeave={handleLeave}
                    >
                        <motion.div
                            className="blog__nav-button-wrapper"
                            style={{ backgroundImage: `url(${blogBg})` }}
                            exit={() => {
                                let rightLink = rightLinkRef.current;
                                return rightLink.classList.contains('active') ? { opacity: 1 } : { opacity: 0 }
                            }}
                            animate={{ opacity: 1 }} transition={{ duration: 0.2, ...transition }}
                        >
                            <motion.h4 exit={{ opacity: 0 }} className="blog__nav-title">
                                <span className="blog__nav-title-text">Next Post</span>
                                <Arrow className="blog__nav-icon" />
                            </motion.h4>
                            <motion.p exit={{ opacity: 0 }} transition={{ duration: 0.2, ...transition }} className="blog__nav-text">Top Candian Web Designers</motion.p>
                        </motion.div>
                    </Link>
                </div>
            </div>
        </motion.main>
    )
}