import React, { useContext, useEffect, useRef } from 'react'
import CustomParticles from './../../../common/CustomParticles/CustomParticles';
import { gsap } from 'gsap/gsap-core';
import SideText from './../../../common/SideText';

import image from '../../../images/blogs/bg.png';
import Date from './../../../common/Date';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import SlideIn from '../../../common/SlideIn';
import PageProgress from '../../../common/PageProgress';
import { LoadedContext } from './../../../providers/LoadedProvider';

let blogs = [
    { title: 'Clutch: Top Candian Web Designers', link: '/blogs/1', date: 'December 25, 2019', text: ' Proin gravida nibh vel velit auctor aliquet. Aenean sollicitudin, lorem quis bibendum auctor, nisi elit consequat ipsum.', id: 1 },
    { title: 'Clutch: Top Candian Web Designers', link: '/blogs/2', date: 'December 25, 2019', text: ' Proin gravida nibh vel velit auctor aliquet. Aenean sollicitudin, lorem quis bibendum auctor, nisi elit consequat ipsum.', id: 2 },
    { title: 'Clutch: Top Candian Web Designers', link: '/blogs/3', date: 'December 25, 2019', text: ' Proin gravida nibh vel velit auctor aliquet. Aenean sollicitudin, lorem quis bibendum auctor, nisi elit consequat ipsum.', id: 3 },
]

let transition = { duration: 0.2, ease: [0.43, 0.13, 0.23, 0.96] };

export default function Blogs({ setLinkProps, handleHover, handleLeave }) {
    let textRefs = useRef([]);
    let loaded = useContext(LoadedContext);

    useEffect(() => {
        window.scrollTo(0, 0);
        let textElements = textRefs.current;

        gsap.timeline({
            defaults: {
                duration: 1,
            },
            scrollTrigger: {
                trigger: textElements[1],
                start: "top center",
                onEnter: () => animateTextLine(textElements)
            }
        });
    }, [])

    function handleLinkClick(e) {
        let link = e.currentTarget;
        let links = document.querySelectorAll('[data-blog-card]');
        links.forEach(otherLink => {
            if (otherLink !== link) {
                otherLink.style.opacity = '0';
            }
        })
        let image = link.querySelector('[data-image]');
        let coords = image.getBoundingClientRect();
        setLinkProps({
            left: coords.left,
            top: coords.top - (document.getElementById('header').clientHeight),
            width: coords.width,
            height: coords.height,
        });

        document.body.classList.add('no-scroll');
    }

    return (
        <motion.main className="main" initial='initial' animate='animate' exit='exit'>
            {loaded ? <PageProgress /> : null}
            <div className="blogs">
                <CustomParticles modificator="blogs" />
                <div className="blogs__wrapper container" id="blogs-wrapper">
                    <SideText modificator="blogs" wrapper="blogs-wrapper">our thoughts</SideText>
                    <div className="blogs__title">our thoughts</div>
                    <div className="line-text blogs__line-text">
                        <div className="line-text__row" ref={el => textRefs.current.push(el)}>
                            We keep you up-to-date on the latest digital
                        </div>
                        <div className="line-text__row" ref={el => textRefs.current.push(el)}>development trends and news.</div>
                    </div>
                    <ul className="blogs__list">
                        {blogs.map(blog => {
                            return (
                                <li className="blogs__item" key={blog.id}>
                                    <SlideIn className="card card--blog" setXPercent={-100} opacity={1}>
                                        <Link to={blog.link} className="card__link" onClick={handleLinkClick} onMouseOver={handleHover} onMouseLeave={handleLeave}>
                                            <div className="card__image-wrapper" style={{ backgroundImage: `url(${image})` }} data-image />
                                            <motion.div exit={{ opacity: 0 }} transition={transition} className="card__column">
                                                <h4 className="card__name">News</h4>
                                                <h1 className="card__title">{blog.title}</h1>
                                                <Date date={blog.date} />
                                                <p className="card__text">{blog.text}</p>
                                            </motion.div>
                                        </Link>
                                    </SlideIn>
                                </li>
                            );
                        })}
                    </ul>
                    <ul className="paginator blogs__paginator">
                        <li className="paginator__item">
                            <a href="22" className="paginator__link paginator__link--prev">
                                <span>Previous</span>
                            </a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link">
                                <span>1</span>
                            </a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link">
                                <span>2</span>
                            </a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link">
                                <span>3</span>
                            </a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link">
                                <span>4</span>
                            </a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link">...</a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link">
                                <span>10</span>
                            </a>
                        </li>
                        <li className="paginator__item">
                            <a href="22" className="paginator__link paginator__link--next">
                                <span>Next</span>
                            </a>
                        </li>
                    </ul>
                </div>
            </div>
        </motion.main>
    )
}

function animateTextLine(textElements) {

    return gsap.timeline({
        defaults: {
            duration: 1,
            ease: 'power4.out'
        }
    })
        .set(textElements, {
            className: 'line-text__row animation-out',
            stagger: 0.5,
            ease: 'Power4.out'
        }, 0)
}
