import React, { useEffect, useRef } from 'react'
import { gsap } from 'gsap/gsap-core';

export default function CaseList({itemIndex, gallery}) {
    let textMasks = useRef([]);
    let texts = useRef([]);
    let image = useRef([]);
    let imageMask = useRef([]);

    useEffect(() => {
        let textMaskElements = textMasks.current;
        let textElements = texts.current;
        let imageMaskElement = imageMask.current;
        let imageElement = image.current;

        initCaseAnimation(textMaskElements, textElements, imageMaskElement, imageElement);
    }, [itemIndex])

    return (
        <ul className="case__list">
            <li className="case__item">
                <h3 className="case__item-title">Client:</h3>
                <div className="case__item-image-mask" ref={el => imageMask.current.push(el)}>
                    <img src={gallery[itemIndex].image} alt="" className="case__item-image" ref={el => image.current.push(el)} />
                </div>
            </li>
            <li className="case__item">
                <h3 className="case__item-title">Accomplishments: </h3>
                <div className="case__item-text-mask" ref={el => textMasks.current.push(el)}>
                    <p className="case__item-text" ref={el => texts.current.push(el)}>
                        {gallery[itemIndex].texts[0]}
                    </p>
                </div>
            </li>
            <li className="case__item">
                <h3 className="case__item-title">Kind Words:</h3>
                <div className="case__item-text-mask" ref={el => textMasks.current.push(el)}>
                    <p className="case__item-text" ref={el => texts.current.push(el)}>
                        {gallery[itemIndex].texts[1]}
                    </p>
                </div>
            </li>
        </ul>
    )
}

function initCaseAnimation(textMaskElements, textElements, imageMaskElement, imageElement) {
    
    let textTl = gsap.timeline({
        defaults: {
            duration: 0.4,
            ease: 'Power2.in'
        }
    });

    textTl
        .from(textElements, {
            yPercent: 100
        }, 0)
        .to(textElements, {
            yPercent: 0
        }, 0)
        .from(textMaskElements, {
            yPercent: -46
        }, 0)
        .from(imageElement, {
            yPercent: 130
        }, 0)
        .from(imageMaskElement, {
            yPercent: -90
        }, 0)
}
