import React, { useEffect, useRef } from 'react';

import circle from '../../../../../images/reputation/circle.png';
import img1 from '../../../../../images/reputation/img1.png';
import img2 from '../../../../../images/reputation/img2.png';
import img3 from '../../../../../images/reputation/img3.png';
import img4 from '../../../../../images/reputation/img4.png';

import gsap from 'gsap';
import CustomParticles from './../../../../../common/CustomParticles/CustomParticles';
import SideText from './../../../../../common/SideText';
import SlideIn from '../../../../../common/SlideIn';

export default function Reputation() {
    let textRefs = useRef([]);
    let imagesWrapper = useRef(null);

    useEffect(() => {
        let textElements = textRefs.current;

        gsap.timeline({
            defaults: {
                duration: 1,
            },
            scrollTrigger: {
                trigger: textElements[1],
                start: "top center",
                onEnter: () => animateTextLine(textElements)
            }
        })
    }, [])

    return (
        <section className="section reputation">
            <CustomParticles modificator="reputation" />
            <div className="reputation__wrapper container" id="reputation-wrapper">
                <SideText modificator="reputation" wrapper="reputation-wrapper">reputation</SideText>
                <div className="line-text reputation__line-text">
                    <div className="line-text__row" ref={el => textRefs.current.push(el)}>
                        We develop innovative&nbsp;
                        <span ref={el => textRefs.current.push(el)}>digitial solutions</span>
                    </div>
                    <div className="line-text__row" ref={el => textRefs.current.push(el)}>for your organizations transformational success</div>
                </div>
                <h2 className="reputation__title">Top Rated Amongst Industry Experts</h2>
                <img src={circle} alt="" className="reputation__circle" />
                <div className="reputation__images" ref={imagesWrapper}>
                    <SlideIn className="reputation__image" opacity={1} delay={0} trigger={imagesWrapper.current}>
                        <img src={img1} alt="" width="100%"/>
                    </SlideIn>
                    <SlideIn className="reputation__image" opacity={1} delay={0.1} trigger={imagesWrapper.current}>
                        <img src={img2} alt="" width="100%"/>
                    </SlideIn>
                    <SlideIn className="reputation__image" opacity={1} delay={0.2} trigger={imagesWrapper.current}>
                        <img src={img3} alt="" width="100%"/>
                    </SlideIn>
                    <SlideIn className="reputation__image" opacity={1} delay={0.3} trigger={imagesWrapper.current}>
                        <img src={img4} alt="" width="100%"/>
                    </SlideIn>
                </div>
            </div>
        </section>
    )
}

function animateTextLine(textElements) {
    return gsap.timeline({
        defaults: {
            duration: 1,
            ease: 'power4.out'
        }
    })
        .set(textElements, {
            className: 'line-text__row animation-out',
            stagger: 0.5,
            ease: 'Power4.out'
        }, 0)
        .set(textElements[0], {
            className: 'line-text__row animation-remove',
        }, 0.5)
        .set(textElements[0], {
            className: 'line-text__row animation-stick',
        }, 1.5)
}
