import React, { useState } from 'react'

import CaseCard from './CaseCard';
import CustomSwiper from '../../CustomSwiper/CustomSwiper';
import Button from '../../Button';
import SideText from '../../SideText';
import CustomParticles from '../../CustomParticles/CustomParticles';
import CaseList from './CaseList';

export default function Case({gallery, particles}) {
    const [itemIndex, setItemIndex] = useState(0);
    
    return (
        <section className="section case" id="case-wrapper">
            <div className="title case__title container">
                <div className="title__cat">Case Studies</div>
                <h1>Recent Breakthroughs</h1>
            </div>
            {particles ? <CustomParticles modificator="case" /> : null}   
            <div className="case__wrapper container">
                <SideText modificator="case" wrapper="case-wrapper">case studies</SideText>
                <div className="case__column">
                    <CaseList itemIndex={itemIndex} gallery={gallery} />
                    <Button type='link' className='case__button' text='View Case Study' href='#' />
                </div>
                <CustomSwiper gallery={gallery} Component={CaseCard} id="case" setItemIndex={setItemIndex} />
            </div>
        </section>

    )
}
