import React, { useContext, useEffect, useRef, useState } from 'react'
import { Link } from 'react-router-dom';
import { gsap } from 'gsap/gsap-core';
import { motion } from 'framer-motion';

import CustomParticles from '../../../common/CustomParticles/CustomParticles';
import ParallaxBanner from '../../../common/ParallaxBanner'
import SideText from '../../../common/SideText';
import Arrow from './../../../common/icons/Arrow';
import SlideIn from './../../../common/SlideIn';
import LadderText from '../../../common/LadderText';

import bg from '../../../images/case-study/bg.png';
import bg2 from '../../../images/case-study/bg2.png';
import bg3 from '../../../images/case-study/bg3.png';
import image from '../../../images/case-study/image.png';
import icon from '../../../images/case-study/icon.png';
import FixedNavSections from '../../../common/FixedNavSections/FixedNavSections';
import itemImg1 from '../../../images/case-study/img1.png';
import itemImg2 from '../../../images/case-study/img2.png';
import itemImg3 from '../../../images/case-study/img3.png';
import cmc from '../../../images/case-study/cmc.png';
import PageProgress from '../../../common/PageProgress';
import { LoadedContext } from './../../../providers/LoadedProvider';
import { CursorContext } from './../../../providers/CursorProvider';

let sections = [
    {
        title: 'Drupal Migration ',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 1
    },
    {
        title: 'Product Assembly',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 2
    },
    {
        title: 'Integration',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 3
    },
    {
        title: 'Submission Portal',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 4
    },
    {
        title: 'Website Optimization',
        text: 'Drupal is a popular web-based content management system designed for small to large enterprises with needs such as complex workflows, multilingual content, and enterprise integrations.',
        image: image,
        icon: icon,
        id: 5
    }
];

let transition = { duration: 0.2, ease: [0.43, 0.13, 0.23, 0.96] };

export default function CaseStudy({ setLinkProps, linkProps, headerRef }) {
    let { handleHover, handleLeave } = useContext(CursorContext);
    let ladderText = useRef([]);
    const [mobileScreen, setMobileScreen] = useState(false);

    let loaded = useContext(LoadedContext);

    function checkForMobileScreen() {
        if (window.innerWidth < 768) {
            setMobileScreen(true);
        } else {
            setMobileScreen(false);
        }
    }

    function handleLinkClick(e) {
        let link = e.currentTarget;
        let image = link.querySelector('[data-image]');
        let coords = image.getBoundingClientRect();
        setLinkProps({
            left: coords.left,
            top: coords.top - (document.getElementById('header').clientHeight),
            width: coords.width,
            height: coords.height,
        });

        document.body.classList.add('no-scroll');
    }

    useEffect(() => {
        checkForMobileScreen();
        document.body.classList.remove('no-scroll');
        let tl = initLadderAnimation(ladderText.current);
        window.addEventListener('resize', checkForMobileScreen);

        return () => {
            tl.kill();
            window.removeEventListener('resize', checkForMobileScreen);
        }
    }, []);

    return (
        <motion.main initial="initial" animate="animate" exit="exit" className="main case-study">
            {loaded ? <PageProgress /> : null}
            <ParallaxBanner
                title="CMC: Candaian Music Centre"
                animateTitle={true} bg={bg}
                className="banner--case-study"
                scrollToID="case-study-wrapper"
                percent={window.innerWidth < 768 ? 50 : 50}
                id="top-banner"
                linkProps={linkProps}
                start={window.innerWidth < 768 ? "top top" : "center center"}
                headerRef={headerRef}
            />
            <div className="case-study__wrapper" id="case-study-wrapper">
                <CustomParticles modificator="case-studies" />
                <div className="case-study__columns container" id="columns">
                    <SideText modificator="case-study" wrapper="columns" percent={10}>challange</SideText>
                    <div className="case-study__column case-study__column--1" id="slide-wrapper">
                        <div className="case-study__mask case-study__mask--title">
                            <SlideIn className="case-study__title case-study__title--v1" trigger={mobileScreen ? "#top-banner" : "#slide-wrapper"} setYPercent={100} toYPercent={0}>The Challange</SlideIn>
                        </div>
                        <div className="case-study__mask case-study__mask--text">
                            <SlideIn className="case-study__text" trigger={mobileScreen ? "#top-banner" : "#slide-wrapper"} setYPercent={100} toYPercent={0}>
                                The Canadian Music Centre holds Canada's largest collection of Canadian concert music. With an ever-growing resource library containing tens of thousands of records, CMC was seeking an expert team to implement an innovative and secure approach to managing and sharing the works of Canadian composers online via their new WordPress website. CMC approached Thinkbound to make their enduring vision a reality.
                            </SlideIn>
                        </div>
                    </div>
                    <div className="case-study__column case-study__column--2 case-study__mask">
                        <SlideIn trigger={mobileScreen ? "#top-banner" : "#slide-wrapper"} setXPercent={100} toXPercent={0}>
                            <h3 className="case-study__title case-study__title--columns case-study__title--small">The Work</h3>
                            <ul className="case-study__work-list">
                                <li className="case-study__work-item">Drupal Migration</li>
                                <li className="case-study__work-item">Product Assembly</li>
                                <li className="case-study__work-item">Integration</li>
                                <li className="case-study__work-item">Submission Portal</li>
                                <li className="case-study__work-item">Website Optimization</li>
                            </ul>
                        </SlideIn>
                    </div>
                </div>
                <FixedNavSections sections={sections} className="case-study" headerRef={headerRef} />
                <ParallaxBanner title="The Result" bg={bg2} className="banner--result" percent={23} id="result-banner" />
                <div className="case-study__result container" id="result">
                    <SideText modificator="result" wrapper="result" fullHeight={true}>the result</SideText>
                    <ul className="case-study__list">
                        <li className="case-study__item">
                            <div className="case-study__item-column case-study__item-column--1">
                                <SlideIn setXPercent={100} toggleActions='restart pause reverse reset' opacity={1} className="case-study__item-image" style={{ backgroundImage: `url(${itemImg1})` }} />
                            </div>
                            <div className="case-study__item-column case-study__item-column--2">
                                <SlideIn setXPercent={-100} toggleActions='restart pause reverse reset' opacity={1} className="case-study__item-wrapper">
                                    <h2 className="case-study__item-title">Software</h2>
                                    <p className="case-study__item-text">
                                        The CMC was left with a leaner operational model with all its software and components working together, along with major collaboration improvements organization-wide. Through the use of Thinkbounds systems, internal and external customers rely significantly less on IT and are refocussed on their organizational goals.
                                    </p>
                                </SlideIn>
                            </div>
                        </li>
                        <li className="case-study__item">
                            <div className="case-study__item-column case-study__item-column--1">
                                <SlideIn setXPercent={-100} toggleActions='restart pause reverse reset' opacity={1} className="case-study__item-image" style={{ backgroundImage: `url(${itemImg2})` }} />
                            </div>
                            <div className="case-study__item-column case-study__item-column--2">
                                <SlideIn setXPercent={100} toggleActions='restart pause reverse reset' opacity={1} className="case-study__item-wrapper">
                                    <h2 className="case-study__item-title">Roadmapping</h2>
                                    <p className="case-study__item-text">
                                        Detailed feedback sessions led to creative problem solving through flowcharts and architecture diagrams and developing roadmaps and implementation of ideal scenarios.
                                    </p>
                                </SlideIn>
                            </div>
                        </li>
                        <li className="case-study__item">
                            <div className="case-study__item-column case-study__item-column--1">
                                <SlideIn setXPercent={100} toggleActions='restart pause reverse reset' opacity={1} className="case-study__item-image" style={{ backgroundImage: `url(${itemImg3})` }} />
                            </div>
                            <div className="case-study__item-column case-study__item-column--2">
                                <SlideIn setXPercent={-100} toggleActions='restart pause reverse reset' opacity={1} className="case-study__item-wrapper">
                                    <h2 className="case-study__item-title">Thinking Beyond</h2>
                                    <p className="case-study__item-text">
                                        Thinkbound maintains an innovation role within the organization as the digital lead for all enhancements and modifications to the CMCs various platforms. With daily communication and weekly reporting.
                                    </p>
                                </SlideIn>
                            </div>
                        </li>
                    </ul>
                </div>
                <div className="case-study__feedback">
                    <div className="case-study__feedback-wrapper container" id="feedback-wrapper">
                        <div className="case-study__feedback-mask case-study__feedback-mask--title">
                            <SlideIn setYPercent={100} trigger="#feedback-wrapper" className="case-study__title case-study__title--feedback">Client Feedback</SlideIn>
                        </div>
                        <LadderText refs={ladderText} className="case-study__feedback-text" text="What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist." />
                        <div className="case-study__feedback-mask case-study__feedback-mask--user">
                            <SlideIn setYPercent={100} trigger="#feedback-wrapper" className="case-study__feedback-user">Glenn Hodgins - President and CEO</SlideIn>
                        </div>
                        <div className="case-study__feedback-mask case-study__feedback-mask--image">
                            <SlideIn setYPercent={100} trigger="#feedback-wrapper" className="case-study__feedback-image">
                                <img src={cmc} alt="" />
                            </SlideIn>
                        </div>
                    </div>
                </div>
                <Link to={"/case-studies/" + Math.random()} className="banner banner--next" onClick={handleLinkClick} onMouseOver={handleHover} onMouseLeave={handleLeave}>
                    <div className="banner__image" style={{ backgroundImage: `url(${bg3})` }} data-image></div>
                    <motion.div exit={{ opacity: 0 }} transition={transition} className="banner__wrapper container">
                        <h1 className="banner__title banner__title--big">Next Project</h1>
                        <h3 className="banner__title banner__title--small">Box Me Up </h3>
                        <div className="banner__title banner__title--arrow">
                            <span className="banner__title-text">View Case study</span>
                            <Arrow className="banner__title-icon" />
                        </div>
                    </motion.div>
                </Link>
            </div>
        </motion.main>
    )
}

function initLadderAnimation(ladderTextElements) {
    console.log(1);
    return gsap.timeline({
        defaults: {
            delay: 0.2,
            duration: 0.4,
            ease: 'Power4.in'
        },
        onComplete: () => {
            document.querySelector('.case-study__feedback-text').classList.add('active');
        },
        scrollTrigger: {
            trigger: '#feedback-wrapper',
            start: 'top center',
        },
    }).from(ladderTextElements, {
        yPercent: 100,
        stagger: 0.1,
    })
}