import React, { useContext, useEffect, useRef } from 'react'

import Banner from './banner/components/Banner';
import Reputation from './reputation/components/Reputation';
import Services from './services/components/Services';
import Industry from './industry/components/Industry';
import Testimonials from './../../../common/Testimonials/components/Testimonials';
import News from './news/components/News';
import Case from './../../../common/Case/components/Case';
import PageProgress from '../../../common/PageProgress';
import { LoadedContext } from '../../../providers/LoadedProvider';
import { ScrollTrigger } from 'gsap/ScrollTrigger';

export default function MainPage({ caseGallery, testimonialsGallery, togglePopup, headerRef, setLinkProps }) {

    let loaded = useContext(LoadedContext);
    const scrollTrigger = useRef(null);
    const bannerRef = useRef(null);

    useEffect(() => {
        let header = headerRef.current;

        scrollTrigger.current = ScrollTrigger.create({
            onUpdate: () => handleScrollUpdate(header, bannerRef.current),
        });

        return () => {
            scrollTrigger.current.kill();
        }
    }, [headerRef]);

    return (
        <div className="main main--pt-0">
            {loaded ? <PageProgress /> : null}
            <Banner bannerRef={bannerRef} togglePopup={togglePopup} />
            <Reputation />
            <Services />
            <Industry />
            <Case gallery={caseGallery} particles={true} />
            <Testimonials gallery={testimonialsGallery} togglePopup={togglePopup} />
            <News setLinkProps={setLinkProps} />
        </div>
    )
}

function handleScrollUpdate(header, banner) {

    setTimeout(() => {
        if (header.classList.contains('header--main')) {
            if (window.scrollY >= banner.clientHeight - header.offsetHeight) {
                header.classList.add('header--white');
            } else {
                header.classList.remove('header--white');
            }
        } else {
            header.classList.add('header--white');
        }
    }, 50)
}
