import React, { useRef } from 'react'

import SideText from '../../SideText'
import thinkboundIcon from '../../../icons/thinkbound.png'
import clutchIcon from '../../../icons/clutch.png'

import CustomSwiper from '../../CustomSwiper/CustomSwiper';
import Button from '../../Button';
import TestimonialsCard from './TestimonialsCard';

export default function Testimonials({gallery, togglePopup}) {
    let caseWrapperRef = useRef(null);

    let options = {
        initialSlide: 1,
        centeredSlides: true,
        spaceBetween: 0,
        effect: 'coverflow',
        coverflowEffect: {
            rotate: 20,
            stretch: 124,
            depth: 250, 
            modifier: 1,
            slideShadows: false,
        },
        loop: true,
        breakpoints: {
            989: {
                coverflowEffect: {
                    stretch: 103,
                },
                slidesPerView: 2,
            },
            767: {
                coverflowEffect: {
                    stretch: 71,
                },
                slidesPerView: 2,
            },
            319: {
                coverflowEffect: {
                    stretch: -100,
                },
                slidesPerView: 1,
            },
        }
    }

    return (
        <section className="section testimonials" ref={caseWrapperRef}>
            <div className="testimonials__wrapper container" id="testimonials-wrapper">
                <div className="title title--center">
                    <div className="title__cat">Phone conducted interviews</div>
                    <h1>Client Feedback</h1>
                </div>
                <SideText modificator="testimonials" wrapper="testimonials-wrapper">testimonials</SideText>
                <CustomSwiper gallery={gallery} Component={TestimonialsCard} id="testimonials" options={options} />
                <div className="testimonials__buttons">
                    <Button type='link' className='testimonials__button' text='View in Clutch' href='#' icon={clutchIcon} />
                    <Button type='button' className='testimonials__button' text='Start Project' icon={thinkboundIcon} onClick={togglePopup} />
                </div>
            </div>
        </section>

    )
}
