import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, } from 'react-router-dom';
import App from './App';
import CursorProvider from './providers/CursorProvider';

ReactDOM.render(
    <React.StrictMode>
        <CursorProvider>
            <Router>
                <App />
            </Router>
        </CursorProvider>
    </React.StrictMode>,
    document.getElementById('root')
);