import './fonts/stylesheet.css';
import './sass/style.scss';

import { Route, Switch, useLocation, withRouter } from 'react-router-dom';

import Header from './modules/header/components/Header';

import { useState, useContext, useEffect, useRef } from 'react';
import MainPage from './modules/main-page/components/MainPage';
import Footer from './modules/footer/components/Footer';
import Blog from './modules/blog/components/Blog';
import { AnimatePresence } from 'framer-motion';
import Blogs from './modules/blogs/components/Blogs';
import Service from './modules/service/components/Service';

import bg from './images/case/card.png'
import itemImage from './images/case/case-image.png';
import testimonialsImage from './images/testimonials/testimonials-card.png'
import Contact from './modules/contact/components/Contact';
import CaseStudies from './modules/case-studies/components/CaseStudies';
import CaseStudy from './modules/case-study/components/CaseStudy';
import LoadedProvider from './providers/LoadedProvider';
import { CursorContext } from './providers/CursorProvider';
import Popup from './common/Popup';
import Form from './common/Form';
import Button from './common/Button';
import thinkboungLogo from './icons/thinkbound.png';
import { checkForScrollbar, getScrollbarWidth } from './services/scrollbarService';
import TransitionProvider from './providers/TransitionProvider';

let caseGallery = [
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 1,
        texts: ['Custom WordPress Visualizer using latest transformations Lasy load and deferred assets', 'Thinkbound Inc. implements impressive technical expertise and a thoughtful problem-solving mindset.'],
        image: itemImage
    },
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 2,
        texts: ['Custom WordPress Visualizer using latestd deferred assets', 'Thinkbound Inc. implements impressive technical exem-solving mindset.'],
        image: itemImage
    },
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 3,
        texts: ['Custom WordPress Visualizer using latest', 'Thinkbound Inc. implements impressivelem-solving mindset.'],
        image: itemImage
    },
    {
        title: 'Vifloor',
        text: 'Web App Development',
        href: '#',
        bg: bg,
        id: 4,
        texts: ['Custom WordPress Visualizer using latest', 'Thinkbound Inc. WordPress implements impressivelem-solving mindset.'],
        image: itemImage
    },
];

let testimonialsGallery = [
    { author: 'Box Me Up Inc.', text: '"Thinkbound checks all of our boxes and provides everything we request."', id: 1, testimonialsImage },
    { author: 'Storage Protectors', text: '"What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist."', id: 2, testimonialsImage },
    { author: 'Box Me Up Inc.', text: '"Thinkbound checks all of our boxes and provides everything we request."', id: 3, testimonialsImage },
    { author: 'Storage Protectors', text: '"What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist."', id: 4, testimonialsImage },
    { author: 'Box Me Up Inc.', text: '"Thinkbound checks all of our boxes and provides everything we request."', id: 5, testimonialsImage },
    { author: 'Storage Protectors', text: '"What makes Thinkbound stand out is their ability to create solutions that dont otherwise exist."', id: 6, testimonialsImage },
];

function App({ ...props }) {
    const location = useLocation();
    const [mobileMenuShow, setMobileMenuShow] = useState(false);
    const [linkProps, setLinkProps] = useState({});
    const [showPopup, setShowPopup] = useState(false);
    let { handleHover, handleLeave, handleHide } = useContext(CursorContext);

    const headerRef = useRef(null);

    function togglePopup() {
        setShowPopup(state => !state);
    }

    useEffect(() => {
        let scrollBarWidth = getScrollbarWidth();

        if ((mobileMenuShow || showPopup) && checkForScrollbar()) {
            document.body.style.paddingRight = scrollBarWidth + 'px';
            document.body.style.overflow = 'hidden';
            headerRef.current.style.paddingRight = scrollBarWidth + 'px';
        } else {
            document.body.style.overflow = null;
            document.body.style.paddingRight = null;
            headerRef.current.style.paddingRight = null;
        }
    }, [mobileMenuShow, showPopup])

    return (
        <TransitionProvider>
            <LoadedProvider>
                <Header
                    history={props.history}
                    mobileMenuShow={mobileMenuShow}
                    setMobileMenuShow={setMobileMenuShow}
                    togglePopup={togglePopup}
                    handleHover={handleHover} handleLeave={handleLeave}
                    headerRef={headerRef}
                />
                <AnimatePresence initial={false} exitBeforeEnter>
                    <Switch location={location} key={location.pathname}>
                        <Route path="/" exact>
                            <MainPage setLinkProps={setLinkProps} headerRef={headerRef} caseGallery={caseGallery} testimonialsGallery={testimonialsGallery} togglePopup={togglePopup} />
                        </Route>
                        <Route path="/blogs/:id">
                            <Blog linkProps={linkProps} setLinkProps={setLinkProps} handleHover={handleHover} handleLeave={handleLeave} handleHide={handleHide} />
                        </Route>
                        <Route path="/blogs">
                            <Blogs setLinkProps={setLinkProps} handleHover={handleHover} handleLeave={handleLeave} />
                        </Route>
                        <Route path="/service">
                            <Service togglePopup={togglePopup} caseGallery={caseGallery} testimonialsGallery={testimonialsGallery} />
                        </Route>
                        <Route path="/contact">
                            <Contact />
                        </Route>
                        <Route path="/case-studies" exact>
                            <CaseStudies headerRef={headerRef} setLinkProps={setLinkProps} />
                        </Route>
                        <Route path="/case-studies/:id">
                            <CaseStudy headerRef={headerRef} setLinkProps={setLinkProps} linkProps={linkProps} />
                        </Route>
                    </Switch>
                </AnimatePresence>
                <Footer togglePopup={togglePopup} handleHover={handleHover} handleLeave={handleLeave} handleHide={handleHide} />
                <Popup className="popup--form" showPopup={showPopup} togglePopup={togglePopup}>
                    <div className="popup__wrapper" data-popup-wrapper>
                        <div className="popup__scrollwrapper">
                            <div className="popup__content">
                                <h1 className="popup__title">
                                    <span>Start Project</span>
                                    <button className="close popup__close" onClick={togglePopup} onMouseOver={handleHover} onMouseLeave={handleLeave} />
                                </h1>
                                <Form className="form--popup" closeForm={showPopup}
                                    Button={<Button type="button" icon={thinkboungLogo} className="form__button" text="Start project" />}
                                />
                            </div>
                        </div>
                    </div>
                </Popup>
                <div className={"background" + (mobileMenuShow ? " active" : "")} />
            </LoadedProvider>
        </TransitionProvider>
    );
}

export default withRouter(App);
