import React from 'react'
import { Link } from 'react-router-dom';
import Date from './../../../../../common/Date';

export default function TestimonialsCard({ item, ...props }) {
    return (
        <Link to={item.to} className="card card--news" data-card {...props}>
            <div className="card__background" style={{ backgroundImage: `url(${item.background})` }} data-image></div>
            <div className="card__wrapper">
                <h1 className="card__title">{item.title}</h1>
                <Date date={item.date} />
            </div>
        </Link>
    )
}
