import React, { useContext, useEffect, useRef } from 'react'
import { gsap } from 'gsap/gsap-core';
import parallaxAnimation from './../services/parallaxAnimation';
import smoothscroll from 'smoothscroll-polyfill';
import { motion } from 'framer-motion';
import { CursorContext } from './../providers/CursorProvider';

smoothscroll.polyfill();

let transition = { duration: 0.2, ease: [0.43, 0.13, 0.23, 0.96] };

export default function ParallaxBanner({ className, bg, title, animateTitle = false, scrollToID, percent, id, linkProps, start, headerRef }) {
    let { handleHover, handleLeave } = useContext(CursorContext);
    let bannerTitle = useRef(null);
    let bannerImage = useRef(null);
    
    function handleArrowClick() {
        let wrapper = document.getElementById(scrollToID);
        let position = wrapper.getBoundingClientRect().top;
        window.scrollTo({
            top: position + window.scrollY - headerRef.current.clientHeight,
            behavior: 'smooth'
        });
    }

    useEffect(() => {
        window.scrollTo({ top: 0, behavior: 'auto' });
     
        parallaxAnimation(id ? `#${id}` : "#banner", percent, start);

        if (animateTitle) {
            initTitleAnimation(bannerTitle.current);
            initBannerImageAnimation(bannerImage.current);
        }
    }, [percent, animateTitle, id, start]);

    if (linkProps) {
        return (
            <div className={"banner banner--v2 " + (className ? className : "")}>
                <div className="banner__wrapper" id={id}>
                    <motion.div
                        initial={{
                            ...linkProps,
                        }}
                        exit={{ opacity: 0 }}
                        animate={{
                            width: '100%',
                            height: '110%',
                            transition: { delay: 0.1, duration: 0.75, transition },
                            left: 0,
                            top: 0
                        }}
                        className="banner__image-wrapper"
                        ref={bannerImage}
                    >
                        <div className="banner__image" style={{ backgroundImage: `url(${bg})` }}></div>
                    </motion.div>
                    <motion.div
                        initial={{ opacity: 0, scale: 0.4 }}
                        animate={{ opacity: 1, scale: 1, transition: { delay: 0.1, duration: 0.75, transition } }}
                        className="banner__wrapper-inner container"
                    >
                        <h1 className="banner__title" ref={bannerTitle}>{title}</h1>
                        <button className="banner__arrow" onClick={handleArrowClick} onMouseOver={handleHover} onMouseLeave={handleLeave}></button>
                    </motion.div>
                </div>
            </div>
        );
    } else {
        return (
            <div className={"banner banner--v2 " + (className ? className : "")}>
                <div className="banner__wrapper" id={animateTitle ? (id || "banner") : null}>
                    <div className="banner__image-wrapper" id={!animateTitle ? (id || "banner") : null} ref={bannerImage}>
                        <div className="banner__image" style={{ backgroundImage: `url(${bg})` }}></div>
                    </div>
                    <div className="banner__wrapper-inner container">
                        <h1 className="banner__title" ref={bannerTitle}>{title}</h1>
                        {scrollToID ? <button className="banner__arrow" onClick={handleArrowClick}></button> : null}
                    </div>
                </div>
            </div>
        );
    }
}

function initTitleAnimation(title) {

    return gsap.to(title, {
        scale: 2,
        opacity: 0,
        ease: 'none',
        scrollTrigger: {
            scrub: true
        }
    });
}

function initBannerImageAnimation(image) {

    return gsap.to(image, {
        scale: 3,
        ease: 'none',
        scrollTrigger: {
            scrub: true
        }
    });
}