import React, { useCallback, useEffect, useRef, useState } from 'react';
import Button from '../../../common/Button';

import thinkboungLogo from '../../../icons/thinkbound.png';
import logo from '../../../images/logo/logo.png';

import gsap from 'gsap';
import Burger from './../../../common/icons/Burger';
import { useLocation } from 'react-router';
import Dropdown from './../../../common/Dropdown';
import { DelayLink } from './../../../common/DelayLink';
import addToRefs from './../../../services/addToRefs';

let navLinks = [
    { title: 'Home', id: 1, to: '/' },
    { title: 'About', id: 2, to: '/' },
    {
        title: 'Services',
        id: 3,
        to: '/service',
        menu: [
            { title: 'Websites + Digital Development', to: '/', submenu: null, id: 1 },
            { title: 'Applications + Integrations', to: '/', submenu: null, id: 2 },
            { title: 'Optimization', to: '/', submenu: null, id: 3 },
            {
                title: 'Brand + Marketing',
                submenu: [
                    { title: 'Brand Identity', to: '/', id: 1 },
                    { title: 'Print Solutions', to: '/', id: 2 },
                    { title: 'Social Media', to: '/', id: 3 },
                    { title: 'Digital Advertising', to: '/', id: 4 },
                ],
                to: '/',
                id: 4
            }
        ]
    },
    { title: 'Industry Success', id: 4, to: '/' },
    { title: 'Showcase', id: 5, to: '/case-studies' },
    { title: 'Contact', id: 6, to: '/contact' }
]

export default function Header({ mobileMenuShow, setMobileMenuShow, togglePopup, history, handleHover, handleLeave, headerRef }) {
    let mobileScreen = window.innerWidth < 990;
    const [mobileButtonShow, setMobileButtonShow] = useState(false);
    const [dropdownOpened, setDropdownOpened] = useState(false);
    const [submenuOpened, setSubmenuOpened] = useState(false);
    const [mobileMenuTl, setMobileMenuTl] = useState(null);

    const location = useLocation();
    const { pathname } = location;
    const currentPage = pathname.split('/')[1];

    const menu = useRef(null);
    const links = useRef([]);
    const dropdown = useRef(null);
    const submenu = useRef(null);

    function handleMobileButtonShow() {
        if (window.innerWidth < 990) {
            setMobileButtonShow(true);
        } else {
            setMobileButtonShow(false);
        }
    }

    let playMobileMenuTimeline = useCallback(() => {
        if (mobileMenuShow) {
            mobileMenuTl.timeScale(1.3);
            mobileMenuTl.reverse();
        } else {
            setMobileMenuShow(true);
            mobileMenuTl.timeScale(1);
            mobileMenuTl.play();
        }
    }, [mobileMenuShow, mobileMenuTl, setMobileMenuShow]);

    let toggleDropdown = useCallback(() => {
        setDropdownOpened((state) => !state);

        gsap.to(dropdown.current, {
            duration: 0.4,
            height: dropdownOpened ? 0 : 'auto',
            ease: 'power1.out'
        });
    }, [dropdownOpened]);

    let toggleSubmenu = useCallback(() => {
        setSubmenuOpened((state) => !state);

        gsap.to(submenu.current, {
            duration: 0.4,
            height: submenuOpened ? 0 : 'auto',
            ease: 'power1.out'
        });
    }, [submenuOpened]);

    let onMobileMenuClick = useCallback(() => {
        if (dropdownOpened) {
            toggleDropdown();
        }
        if (submenuOpened) {
            toggleSubmenu();
        }
        playMobileMenuTimeline();
    }, [submenuOpened, dropdownOpened, playMobileMenuTimeline, toggleDropdown, toggleSubmenu]);

    useEffect(() => {

        if (mobileScreen && !mobileMenuTl) {
            setMobileMenuTl(initMobileMenuTl(links.current, menu.current, mobileMenuShow, setMobileMenuShow));
        }

        handleMobileButtonShow();

        let unlisten = (mobileScreen && mobileMenuShow) ? history.listen(() => {
            onMobileMenuClick();
            window.scrollTo(0, 0);
        }) : null;

        window.addEventListener('resize', handleMobileButtonShow);

        return () => {
            window.removeEventListener('resize', handleMobileButtonShow);

            if (mobileScreen && mobileMenuShow) {
                unlisten();
            }
        };
    }, [mobileScreen, history, mobileMenuShow, mobileMenuTl, setMobileMenuShow, onMobileMenuClick]);

    return (
        <header className={"header" + (currentPage ? "" : " header--main")} ref={headerRef} id="header">
            <div className="header__wrapper container container--header">
                <DelayLink delay={500} to="/" className="logo" onMouseOver={handleHover} onMouseLeave={handleLeave}>
                    <img src={logo} alt="" className="logo__image" />
                </DelayLink>
                <div className={"header__menu" + (mobileMenuShow ? " opened" : "")} ref={menu}>
                    <div className="header__menu-wrapper">
                        <ul className="header__menu-list">
                            {navLinks.map(item => {
                                return (
                                    <li
                                        onMouseEnter={(item.menu && !mobileScreen) ? toggleDropdown : () => false}
                                        onMouseLeave={(item.menu && !mobileScreen) ? toggleDropdown : () => false}
                                        key={item.id}
                                        className="header__menu-item"
                                    >
                                        <div className="header__menu-item-wrapper" ref={el => addToRefs(el, links)}>
                                            <DelayLink delay={500}
                                                to={item.to}
                                                className={"header__menu-link" + ((item.to === '/' + currentPage) ? " active" : "")}
                                                onMouseOver={handleHover}
                                                onMouseLeave={(e) => {
                                                    handleLeave();
                                                    addLinkClass(e);
                                                }}
                                                onTransitionEnd={removeLinkClass}          
                                            >
                                                {item.title}
                                            </DelayLink>
                                            {item.menu && <button className={"open open--header" + (dropdownOpened ? " active" : "")} onClick={toggleDropdown}></button>}
                                            {item.menu && <Dropdown
                                                dropdown={dropdown}
                                                list={item.menu}
                                                className="dropdown--header"
                                                handleHover={handleHover}
                                                handleLeave={handleLeave}
                                                submenu={submenu}
                                                toggleSubmenu={toggleSubmenu}
                                                submenuOpened={submenuOpened}
                                                mobileScreen={mobileScreen}
                                                dropdownOpened={dropdownOpened}
                                            />}
                                        </div>
                                    </li>
                                );
                            })}
                        </ul>
                    </div>
                </div>
                <Button type='button' icon={thinkboungLogo} className="header__button" text="Start Project" onClick={togglePopup} />
                {mobileButtonShow ?
                    <button className={"header__mobile-button" + (mobileMenuShow ? " opened" : "")} onClick={onMobileMenuClick}>
                        <Burger className="header__mobile-icon" />
                    </button> : null}
            </div>
        </header>
    )
}

function addLinkClass(e) {
    e.target.classList.add('animate-out');
}

function removeLinkClass(e) {
    e.target.classList.remove('animate-out');
}

function initMobileMenuTl(menuLinks, menu, mobileMenuShow, setMobileMenuShow) {
    gsap.set(menuLinks, {
        yPercent: 100
    })

    let tl = gsap.timeline({
        defaults: {
            ease: 'Power4.out'
        },
        paused: true,
        onReverseComplete: () => {
            setMobileMenuShow(false);
        }
    })
        .to(menu, {
            height: '100%'
        })
        .add(
            gsap.timeline({
                defaults: {
                    stagger: 0.1
                }
            })
                .to(menuLinks, {
                    yPercent: -25,
                    ease: 'power3.out',
                    duration: 0.4
                }, 0)
                .to(menuLinks, {
                    yPercent: 0,
                    duration: 0.3,
                    ease: !mobileMenuShow ? 'power1.in' : 'power1.out',
                }, 0.2)
        )

    return tl;
}