import React, { useEffect, useRef } from 'react'
import thinkboungLogo from '../../../../../icons/thinkbound.png'
import gear from '../../../../../icons/gear.png'
import video from '../../../../../video/video.mp4'

import gsap from 'gsap';
import LadderText from '../../../../../common/LadderText'
import Button from './../../../../../common/Button';
import parallaxAnimation from './../../../../../services/parallaxAnimation';
import addToRefs from './../../../../../services/addToRefs';

export default function Banner({ togglePopup, bannerRef }) {
    const titleMasks = useRef([]);
    const titleTexts = useRef([]);
    const ladderTexts = useRef([]);
    const bannerWrapper = useRef(null);
    const videoWrapper = useRef(null);
    const bannerContent = useRef(null);
    const buttons = useRef([]);

    function initBannerTextAnimation() {


        let textTl = gsap.timeline({
            defaults: {
                duration: 0.7,
                ease: 'Power4.in'
            },
            delay: 0.5
        });

        textTl
            .from(titleTexts.current, {
                yPercent: 100, stagger: 0.1
            }, 0)
            .to(titleTexts.current, {
                yPercent: -4, stagger: 0.1
            }, 0)
            .from(titleMasks.current, {
                yPercent: -46, stagger: 0.1
            }, 0)

        let ladderTl = gsap.timeline({
            defaults: {
                duration: 0.2,
                ease: 'Power4.in'
            }
        });

        ladderTl.from(ladderTexts.current, {
            yPercent: 100,
            stagger: 0.1
        });

        let buttonsTl = gsap.timeline({
            defaults: {
                duration: 0.2,
                ease: 'Power4.in'
            }
        }).to(buttons.current, {
            yPercent: -100,
            stagger: 0.2
        })

        let bannerTl = gsap.timeline();

        bannerTl
            .add(textTl)
            .add(ladderTl)
            .add(buttonsTl);
    }

    useEffect(() => {
        initBannerTextAnimation();
        initBannerVideoScale(videoWrapper.current, bannerRef.current);
        parallaxAnimation(bannerWrapper.current, 60, "center center");
        initContentanimation(bannerContent.current, bannerRef.current);
    }, [bannerRef])

    return (
        <section className="banner banner--main" ref={bannerRef}>
            <div className="banner__wrapper" ref={bannerWrapper}>
                <div className="container">
                    <video className="video" autoPlay muted loop ref={videoWrapper} playsInline>
                        <source src={video} type="video/mp4" />
                    </video>
                    <div ref={bannerContent}>
                        <h1 className="banner__title">
                            <div ref={el => addToRefs(el, titleMasks)} className="banner__title-text-mask">
                                <div ref={el => addToRefs(el, titleTexts)} className="banner__title-text">get to</div>
                            </div>
                            <div ref={el => addToRefs(el, titleMasks)} className="banner__title-text-mask">
                                <div ref={el => addToRefs(el, titleTexts)} className="banner__title-text banner__title-text--blue">breakthrough</div>
                            </div>
                        </h1>
                        <LadderText
                            refs={ladderTexts}
                            className="banner__text"
                            text="We break through boundaries your organization faces in an  everchanging world "
                        />
                        <div className="banner__buttons">
                            <div className="banner__button-wrapper" ref={el => addToRefs(el, buttons)}> 
                                <Button className='banner__button' type='button' icon={thinkboungLogo} text='Start Project' onClick={togglePopup} />
                            </div>
                            <div className="banner__button-wrapper" ref={el => addToRefs(el, buttons)}>
                                <Button className='banner__button button--white button--transparent' type='link' icon={gear} text='View all Services' href='/service' />
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </section>

    )
}

function initBannerVideoScale(video, trigger) {

    return gsap.to(video, {
        scale: 1.5,
        ease: 'none',
        scrollTrigger: {
            scrub: true,
            trigger: trigger,
            start: 'top top'
        }
    });
}

function initContentanimation(bannerContent, trigger) {

    return gsap.to(bannerContent, {
        opacity: 0,
        ease: 'none',
        scrollTrigger: {
            scrub: true,
            trigger: trigger,
            start: 'top top'
        }
    });
}